/*           ///////////////////////// //   HOME   ////////////// */
.home {
    background: url(/static/media/bg.a8b04f57.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 100vh;
    width: 100%;
}

.header {
    /* background: #fff; */
    color: #fff;
}
.nav__link {
    /* color: # */
    text-transform: uppercase;
    font-weight: 600;
    color: #fff !important;
}
.nav__link.active {
    color: #28a7e9 !important;
}



/* /////////////  Banner */
.banner .banner__title {
    font-size: 4rem;
    font-weight: 700;
    color: #28a7e9;
}
.banner__social {
    display: flex;
    justify-content: center;
}
.banner__social li {
    list-style: none;
    margin: 0 10px;
    transition: all .4s ease-in-out;
}
.banner__social li a {
    color: #fff;
}
.banner__social li:hover {
    transform: scale(1.4);
}
.about .about__picture {
    text-align: center;

}

.about_link {
    text-decoration: none;
    padding: 10px 20px;
    color: #000;
    text-decoration: none;
    background: #764ABC;
    color: #fff;
    position: relative;
    top: 40px;
}
.about_link:hover {
    color: #fff;
    opacity: .8;
} 
.about__title {
    font-weight: 500;
}


@media only screen and (max-width:570px) {
    .about .about__title{
        text-align: center;
    }
}
.portfolio {
  margin-bottom: 100px;
}
.portfolio__header {
    text-align: center;
    margin: 30px;
}
.portfolio__title {
  font-size: 1.4rem;
  text-transform: uppercase;
}
.portfolio__thumb {
  position: relative;
  /* width: 100%; */
  height: 100%;
  cursor: pointer;
  border: 2px solid #ffff;
}
.portfolio__thumb img {
  display: block;
  width: 100%;
  height: 100%;
  background-size: cover;
}

.portfolio__thumb .portfolio__overlay {
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  background-color: #04aa6d9f;
}

.portfolio__thumb:hover .portfolio__overlay {
  opacity: 1;
}

.portfolio__title {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}


@media only screen and (max-width:640px) {
  .portfolio__thumb {
      text-align: center;
      margin: auto 10px;
  }
}
.skill {
    /* background: #; */
}

.skill__progressbar {
    width: 100px;
    height: 100px;
    margin: 20px auto 0 auto;
    display: flex;
    justify-content: center;
}
.skill_progress_img {
    width: 30px;
